<template>
<!-- https://ip-api.com/ -->
    <div class="chatroom">
        <div class="content">
            <div class="item" v-for="item in list" :key="item">
                <div class="ip">{{ item.ipAddress }}</div>
                <div class="text">{{ item.content }}</div>
            </div>
        </div>
        <div class="sent">
            <div class="match"></div>
            <input v-model="matchInfo" type="text">
            <button @click="post">post</button>
        </div>
    </div>
</template>

<script>
import { db, dbBefore } from '../firebase/init.js'

console.log(dbBefore)
export default {
    name: 'Home',
    data() {
        return {
            ip: null,
            matchInfo: null,
            postTime: null,
            postTimeSecond: null,
            list: [],
        }
    },
    methods: {
        post() {
            const hour = new Date().getHours(Date.now())
            const minute = new Date().getMinutes(Date.now())
            const second = new Date().getSeconds(Date.now())
            this.postTime = `${hour}:${minute}:${second}`
            this.postTimeSecond = hour * 3600 + minute * 60 + second
            db.collection('person').doc(this.ip).set({
                ipAddress: this.ip,
                content: this.matchInfo,
                created: this.postTimeSecond,
            })
            db.collection('person')
                .orderBy('created')
                .get()
                .then((querySnapshot) => {
                    this.list = []
                    querySnapshot.forEach((queryDocumentSnapshot) => {
                        const pc = queryDocumentSnapshot.data()
                        this.list.push(pc)
                    })
                })
            // fetch('https://worldtimeapi.org/api/ip')
            //     .then((response) => response.json())
            //     .then((timeNow) => {
            //         const hour = new Date().getHours(timeNow.datetime)
            //         const minute = new Date().getMinutes(timeNow.datetime)
            //         const second = new Date().getSeconds(timeNow.datetime)
            //         this.postTime = `${hour}:${minute}:${second}`
            //         this.postTimeSecond = hour * 3600 + minute * 60 + second
            //         console.log(55555)
            //     })
            //     .then(() => {
            //         db.collection('person').doc(this.ip).set({
            //             ipAddress: this.ip,
            //             content: this.matchInfo,
            //             created: this.postTimeSecond,
            //         })
            //     })
            //     .then(() => {
            //         db.collection('person')
            //             .orderBy('created')
            //             .get()
            //             .then((querySnapshot) => {
            //                 querySnapshot.forEach((queryDocumentSnapshot) => {
            //                     const pc = queryDocumentSnapshot.data()
            //                     this.list.push(pc)
            //                 })
            //             })
            //     })
            // delete data
        },
        time() {
        },
    },
    created() {
        fetch('https://api.ipify.org/?format=json')
            .then((response) => response.json())
            .then((data) => {
                this.ip = data.ip
            })
        // delete
        // fetch('https://worldtimeapi.org/api/ip')
        //     .then((response) => response.json())
        //     .then((timeNow) => {
        //         console.log(new Date().getHours(timeNow.datetime),
        //             new Date().getMinutes(timeNow.datetime),
        //             new Date().getSeconds(timeNow.datetime))
        //     })
        db.collection('person')
            .orderBy('created')
            .get()
            .then((querySnapshot) => {
                querySnapshot.forEach((queryDocumentSnapshot) => {
                    const pc = queryDocumentSnapshot.data()
                    this.list.push(pc)
                })
            })
        // delete
        dbBefore.collection('state').doc('del').get().then((documentSnapshot) => {
            // 取得上一時段，刪除狀態
            const action = documentSnapshot.get('action')
            // action 處於 false 還沒刪除， 處於 true 代表完成
            const targetDel = dbBefore.collection('person')
            if (!action) {
                // 處於未完成
                dbBefore.collection('person').get((querySnapshot) => {
                    querySnapshot.forEach((queryDocumentSnapshot) => {
                        targetDel.doc(queryDocumentSnapshot.id).delete()
                    })
                })
                db.collection('state').doc('del').set({ action: false })
            }
        })
    },
}
</script>

<style lang="scss" scoped>
.item {
    display: flex;
    padding: 5px;

    border: 1px solid black;
    // justify-content: center;
    .ip {
    }
}
</style>
